  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  font-weight: bold;
  text-transform: uppercase; }
  .catalog__list li:not(:first-child) {
    margin-left: 1%; }
  .catalog__list li a {
    position: relative;
    padding: 10px 5px;
    display: block; }
    .catalog__list li a::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #b98348;
      transform: scaleX(0);
      transition: all 0.5s;
      transform-origin: left; }
    .catalog__list li a::after {
      content: " ";
      position: absolute;
      top: calc(100% - 3px);
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #b98348;
      transform: scaleX(0);
      transition: all 0.5s;
      transform-origin: right; }
    .catalog__list li a:hover::before, .catalog__list li a:hover::after {
      transform: scaleX(1); }

